import React from "react";
import {Link} from "react-router-dom";

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
    //refs
    //bindings
  }

  render() {
    return (
      <header className="header">
        <label className="headerLabel">Prototyp webovho klienta vyhledvae intermodlnch dopravch spojen:</label>
        <label className="headerLabel"><Link to="/form">Formulov typ</Link>/<Link to="/wizard">Klient typu Wizard</Link></label>
      </header>
    );
  }
}

export default Header;
